(function() {
  'use strict';

  angular
    .module('portfolio')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1445447701305;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();


angular
  .module('portfolio')
  .controller('PostController', PostController);

  PostController.$inject = ['$scope', '$rootScope', '$state', '$stateParams', 'toastr', 'PostService', 'Slug', 'ngDialog'];

  function PostController($scope, $rootScope, $state, $stateParams, toastr, PostService, Slug, ngDialog) {

    var vm = this;

    vm.findOne = function() {
      PostService.findOne($stateParams.slug).then(function(data) {
        vm.postData = data.data;
        console.log(vm.postData);
      });
    }

    vm.findAll = function() {
      PostService.findAll().then(function(data) {
        vm.posts = data.data;
      });
    }

    vm.addPost = function(postData) {
      if(postData.title.length > 0 && postData.content.length > 0){
        postData.slug = Slug.slugify(postData.title);
        PostService.addPost(postData).then(function(data) {
          toastr.success('The post has been created successfully.');
          $state.go('adminDashboard');
        });
      }
    }

    vm.updatePost = function(postData) {
      if(postData.title.length > 0 && postData.content.length > 0){
        PostService.updatePost(postData).then(function(data) {
          toastr.success('The post has been updated successfully.');
          $state.go('adminDashboard');
        });
      }
    }

    vm.deletePost = function(id) {
      //ngDialog.open({ template: 'templateId' });
      PostService.deletePost(id).then(function(data) {
        if (data.data.status){
          toastr.success('The post has been deleted successfully.');
          vm.findAll();
        }
      });
    }
  }


angular
  .module('portfolio')
  .controller('LoginController', LoginController);

  LoginController.$inject = ['$scope', '$rootScope', '$state', 'LoginService'];

  function LoginController ($scope, $rootScope, $state, LoginService) {

    var vm = this;
    vm.loginError = false;

    vm.login = function (loginData) {
      LoginService.checkLogin(loginData.username, loginData.password).then(function(data) {
        if(data.data.status) {
          $rootScope.userData = data.data.user;
          $state.go('adminDashboard');
        }else{
          vm.loginError = true;
        }
      });

    }


  }

(function() {
  'use strict';

  angular
    .module('portfolio')
    .run(runBlock)
    .animation('.reveal-animation', function() {
  return {
    enter: function(element, done) {
      element.css('display', 'none');
      element.fadeIn(500, done);
      return function() {
        element.stop();
      }
    },
    leave: function(element, done) {
      element.fadeOut(500, done)
      return function() {
        element.stop();
      }
    }
  }
});

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('portfolio')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      .state('about', {
        url: '/about',
        templateUrl: 'app/main/about.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('portfolio', {
        url: '/portfolio',
        templateUrl: 'app/main/portfolio.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('blog', {
        url: '/blog',
        templateUrl: 'app/main/blog.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('viewPost', {
        url: '/blog/post/:slug',
        templateUrl: 'app/main/viewPost.html',
        controller: 'PostController',
        controllerAs: 'blogPost'
      })
      .state('adminLogin', {
        url: '/admin/login',
        templateUrl: 'app/admin/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('adminDashboard', {
        url: '/admin/dashboard',
        templateUrl: 'app/admin/dashboard.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('addPost', {
        url: '/admin/post/add',
        templateUrl: 'app/admin/addPost.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      .state('updatePost', {
        url: '/admin/post/update/:slug',
        templateUrl: 'app/admin/editPost.html',
        controller: 'PostController',
        controllerAs: 'post'
      })
      ;


    $urlRouterProvider.otherwise('/');
  }

})();

